adas, jaleas y frutas en almíbar. Dicho método consiste en poner a hervir la fruta en agua y al poco tiempo se le adiciona el azúcar o sacarosa en relación a la cantidad de producto y la concentración que se desea obtener.")])])]),_c('div',{staticClass:"row justify-content-center",attrs:{"titulo":"Adición de alcohol"}},[_c('div',{staticClass:"align-content-center"},[_c('p',[_vm._v("Esta técnica es muy usada para conservar frutas y hortalizas y se hace de forma artesanal, tiene como objetivo que el alcohol ayude a conservar el alimento al penetrarlo, reemplazando los líquidos que este contiene inicialmente. Algunas veces este método se hace en combinación con azúcar y consiste en perforar la fruta con una aguja para que penetre mejor la solución de alcohol y azúcar, y así conservar mejor el producto. Esta técnica es muy usada en la preparación de licores de frutas.")])])]),_c('div',{staticClass:"row justify-content-center",attrs:{"titulo":"Escaldado"}},[_c('div',{staticClass:"align-content-center"},[_c('p',[_vm._v("Esta operación es previa a la realización de una conserva, con ella se busca inactivar enzimas y reducir la carga bacteriana, el escaldo también se puede decir que es un tratamiento térmico, se realiza a 100 ºC y ayuda a acentuar el color de los alimentos y reblandecimiento de tejidos. Para inactivar las enzimas, es importante efectuar un calentamiento rápido bajo una temperatura y tiempo determinado.")])])])])],1),_vm._m(14),_vm._m(15)]),_vm._m(16),_vm._m(17),_vm._m(18),_vm._m(19)],1)],1)
